function input(props){

    return (
        <div>
            <textarea
                id='editor'
                value={props.input}
                onChange={props.change}
                placeholder={`
                    Welcome to my Markdown Previewer
                    ==
                    I built this with Next.js and marked.js

                    *I didn't need React for this simple project*

                    **I used it because I wanted to!**
                `}
                />
            <style jsx>{`
                #editor {
                    width: 800px;
                    height: 200px;
                    margin: auto;
                } 
            `}</style>
        </div>
    )
}

export default input;